.NavToggle {
  width: 20px;
  height: 12px;
  margin-left: auto;
  position: relative;
  cursor: pointer;
  transform: rotate(0deg);
  transition: all 0.25s ease-in-out;
}

@media only screen and (min-width: 1366px) {
  .NavToggle {
    display: none;
  }
}

.NavToggle .NavToggle-icon span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #333;
  border-radius: 0;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: all 0.25s ease-in-out;
}

.NavToggle .NavToggle-icon span:nth-child(1) {
  top: 0px;
}

.NavToggle .NavToggle-icon span:nth-child(2), .NavToggle .NavToggle-icon span:nth-child(3) {
  top: 5px;
}

.NavToggle .NavToggle-icon span:nth-child(4) {
  top: 10px;
}

.NavToggle.Nav--is-visible .NavToggle-icon span:nth-child(1) {
  opacity: 0;
}

.NavToggle.Nav--is-visible .NavToggle-icon span:nth-child(2) {
  transform: rotate(45deg);
}

.NavToggle.Nav--is-visible .NavToggle-icon span:nth-child(3) {
  transform: rotate(-45deg);
}

.NavToggle.Nav--is-visible .NavToggle-icon span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.Nav--primary {
  background: #333;
  overflow-x: hidden;
  overflow-y: auto;
  position: fixed;
  bottom: 0;
  top: 0;
  transition: right 0.5s ease-in-out;
